<template>
	<div id="app">
		<test></test>
		<big-img></big-img>
		<!--<song></song>-->
	</div>
</template>

<script>
	import test from "@/components/double";
	import bigImg from "@/components/bigImg";
	import song from "@/components/song";
	
	export default {
		name: "App",
		data() {
			return {
				
			};
		},
		computed: {},
		mounted() {
			
		},
		computed: {
			isShowList() {
				return this.$store.state.isShowList;
			}
		},
		methods: {
			preventEvent() {
				console.log('test');
			},
			changeScreen(){
				let w = window.innerWidth;
				let h = window.innerHeight;
				this.isHorizon = w > h;
				let isLongScreen = 1.8 * w < h;
				this.$store.commit('changeScreen',isLongScreen);
			}
		},
		components: {
			test,
			bigImg,
			song
		}
	};
</script>

<style lang="less">
	
</style>


